body {
  margin: 0;
}

.container {
  display: grid;
  font-family: Roboto;
  grid-template-columns: 2fr 1fr 1fr;
  grid-template-rows: 800px 300px 380px 420px 80px;
}

.itemA {
  grid-area: mission;
  font-family: Rubik;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  margin: auto 14px auto 125px;
  /* background-color: rgb(245, 204, 142); */
}

  .itemA h1 {
    font-size: 65px;
    margin-bottom: 0;
  }

  .red {
    color: #F55E61;
    font-size: 22px;
    font-family: Roboto;
    margin: 5px 0 20px 10px;
  }

  .missionText {
    font-family: Roboto Light;
    font-size: 20px;
    width: 80%;
    margin: 0 0 0 10px;
  }

  .start {
    height: 35px;
    width: 220px;
    background-color: #F55E61;
    color: white;
    font-size: 20px;
    text-align: center;
    margin: 30px 0 0 30px;
    padding-top: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
  }

  .featText {
    font-family: Roboto Light;
    font-size: 15px;
    width: 80%;
    margin-top: -10px;
  }

.itemB {
  grid-area: photo;
  grid-column: 2 / 4;
  grid-row: 1 / 2;
}

  .faces {
    margin: 160px 0 0 30px;
    height: 60%;
    position: absolute;
    /* background-color: rgb(215, 245, 250); */
  }

.itemC {
  grid-area: features;
  font-family: Roboto;
  grid-column: 1 / 2;
  grid-row: 3 / 5;
  margin: auto 15px auto 250px;
  width: 50%;
  /* background-color: rgb(213, 176, 248); */
}

.itemD {
  grid-area: feat1;
  grid-column: 2 / 3;
  grid-row: 3 / 4;
  width: 80%;
  /* background-color: rgb(244, 247, 211); */
}

  .itemD img {
    width: 200px;
  }

.itemE {
  grid-area: feat2;
  /* background-color: rgb(248, 253, 169); */
  grid-column: 3 / 4;
  grid-row: 3 / 4;
  width: 80%;
  margin-left: -40px;
}

  .itemE img {
    width: 200px;
  }

.itemF {
  grid-area: feat3;
  /* background-color: rgb(204, 203, 140); */
  grid-column: 2 / 3;
  grid-row: 4 / 5;
  width: 80%;
}

  .itemF img {
    width: 200px;
  }

.itemG {
  grid-area: feat4;
  /* background-color: rgb(241, 230, 179); */
  grid-column: 3 / 4;
  grid-row: 4 / 5;
  width: 80%;
  margin-left: -40px;
}

  .itemG img {
    width: 200px;
  }

  .button {
    /* background-color: #4caf50; */
    /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 4px;
    font-size: 16px;
    margin: 30px;
  }

  .bigCircle {
    margin: -120px 0 0 -250px;
    position: absolute;
    /* To push circle to background */
    z-index: -1;
  }

  .pinkCircle {
    height: 500px;
  }

.itemX {
  grid-column: 1 / 4;
  grid-row: 2 / 3;
  /* background-color: yellow; */
}

  .dots2 {
    width: 200px;
    margin-left: 100px;
  }

  .moreDiv{
    text-align: center;
    margin: -150px auto;
    color: #F55E61;
    line-height: 0;
    font-size: 16px;
  }

  .more {
    width: 80px;
  }


/* ==========================================
   =================  Navbar  ===============
   ========================================== */

.navbar {
  height: 65px;
  font-family: sans-serif;
  border-bottom: .5px solid #DDDDDD;
}

.navUl {
  float: right;
  font-size: 16px;
  margin: 20px 10px 0 0;
  /* border: 1px solid green; */
}

.navLi {
  display: inline-block;
  margin-right: 28px;
  /* border: 1px solid blue; */
}

.logo {
  height: 33px;
  margin: 15px 75px 0 50px;
  float: left;
  /* border: 1px solid blue; */
}

.signUp {
  border: 1px solid #F55E61;
  text-align: center;
  height: 22px;
  width: 100px;
  padding-top: 5px;
}

/* ==========================================
   ==============  NavbarMember  ============
   ========================================== */

.Ul {
  float: right;
  font-size: 16px;
  margin: 10px 20px 0 0;
  /* border: 1px solid green; */
}

.Li {
  margin-top: 0;
  display: inline-block;
  margin-right: 35px;
  /* border: 1px solid blue; */
}

.search {
  width: 280px;
  height: 30px;
  margin: 18px 0 0 auto;
  float: left;
  border: 1px solid #F55E61;
}

.searchText {
  color: #9b9292;
  font-size: 11px;
  padding-left: 20px;
}

.magnifier {
  height: 32px;
  width: 32px;
  color: white;
  float: right;
  text-align: center;
  font-size: 20px;
  margin: -12px -30px 0 0;
  background-color: #F55E61;
  transform: rotate(90deg);
  /* border: 1px solid yellow; */
}

.profileCrop {
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  /* border: 1px solid blue; */
}

.miniProfile {
  height: 45px;
}

/* ===================================
   =============  footer  ============
   =================================== */


.footer {
  height: 180px;
  background-color: #322F2E;
  color: white;
  font-family: Roboto;
  /* font-weight: lighter; */
  font-style: 9px;
  /* padding: 100px 0 0 0px; */
}

.footer p {
  float: right;
  margin: 110px 60px 0 0;
}
