/* How it work page */

.memberContainer {
  display: grid;
  font-family: Roboto;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 350px 350px 350px 400px;
  /* border: 2px solid red; */
}

