/*
===============================================
*/

.marginLeft {
  margin-left: -20px;
}

.bubbles {
  position: relative;
  height: 420px;
  margin: -80px 0 0 -60px;
  /* border: 1px solid blue; */
}

.yellowCircleWrap {
  margin: 0 0 0 -60px;
  position: absolute;
  /* To push circle to background */
  z-index: -1;
}

.yellowCircle {
  height: 200px;
}


/* ==================================
=============== Event ===============
===================================== */


.ad {
  width: 70%;
  margin: auto;
  text-align: left;
  /* border: 2px solid red; */
}

.sponserTitle {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 20px;
}

.eventPicWrap {
  /* border: 2px solid green; */
}

.eventPic {
  overflow: hidden;
  border-radius: 10px;
  width: 99%;
  margin: auto;
}

.redText {
  font-size: 14px;
  color: #F55E61;
}

.eventText {
  font-size: 12px;
  color: black;
}


/* ==================================
=============== Members =============
===================================== */


.janeProfile {
  font-family: PT sans;
  font-size: 18px;
  color: #525252;
  width: 70%;
  border: 1px solid #EEEEEE;
  box-sizing: border-box;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  margin: 40px auto;
}

.profileCrop {
  border-radius: 50%;
  overflow: hidden;
  width: 130px;
  height: 130px;
  margin: 20px auto;
  /* border: 1px solid blue; */
}

.janePic {
  height: 210px;
  /* border: 1px solid rgb(128, 0, 122); */
}

.nameTitle {
  text-align: center;
}

.borderBottom {
  border-bottom: 2px solid rgb(218, 216, 216);
  position: relative;
  width: 90%;
  margin: 25px auto;
}

.memberUl {
  list-style: none;
  font-family: PT sans;
  font-size: 17px;
  color: grey;
  margin: 40px auto;
  overflow: scroll;
}

.memberLi {
  margin-bottom: 20px;
}

.boxRed {
  width: 80%;
  height: 90%;
  border-radius: 5%;
  margin: 15px auto;
  border: 2px solid #F55E61;
  font-family: PT sans;
}

.boxPink {
  width: 80%;
  height: 90%;
  margin: 15px auto;
  border: 3px solid #F5EDED;
  box-sizing: border-box;
  box-shadow: 0px 2px 0px #F5EDED;
  border-radius: 10px;
  font-family: PT sans;
}

.request {
  /* position: absolute; */
  width: 145px;
  height: 25px;
  margin: auto;
  text-align: center;
  background-color: #F55E61;
  color: white;
  border-radius: 5px;
  padding-top: 5px;
}